import React from 'react';
import {Element} from "react-scroll"; 
import Linearprogress from "@material-ui/core/Linearprogress";
import "./Skillscontainer.css";
import {Fade, Roll} from "react-reveal";





export const Skillscontainer = () => {
    return (
      
       <Element className="skillscontainer" id="skills">
<div className="skillcontainer__image">
<Fade top>
<img src="https://creazilla-store.fra1.digitaloceanspaces.com/cliparts/59578/laptop-clipart-md.png"></img>
</Fade>
</div>


<div className="skillscontainer__text">
<Roll>
    <h2>Skillset</h2>
    </Roll>
    <div className="skillcontainer__skillset">
    <Roll>
        <h5>REACT JS</h5>
        </Roll>
        <div className="skillcontainer__slider skillcontainer__slider1">
        <Roll>
      <Linearprogress variant="determinate" value={60} />
      </Roll>
        </div>
    </div>
    <div className="skillcontainer__skillset">
    <Roll>
        <h5>BOOTSTRAP</h5>
        </Roll>
        <div className="skillcontainer__slider skillcontainer__slider2">
          <Roll>
      <Linearprogress variant="determinate" value={75} />
      </Roll>
        </div>
    </div>
    <div className="skillcontainer__skillset">
    <Roll>
        <h5>HTML</h5>
        </Roll>
        <div className="skillcontainer__slider skillcontainer__slider3">
        <Roll>
      <Linearprogress variant="determinate" value={90} />
      </Roll>
        </div>
    </div>
    <div className="skillcontainer__skillset">
        <h5>CSS</h5>
        <div className="skillcontainer__slider skillcontainer__slider4">
      <Linearprogress variant="determinate" value={90} />
        </div>
    </div>
    <div className="skillcontainer__skillset">
    <Roll>
        <h5>JAVASCRIPT</h5>
        </Roll>
        <div className="skillcontainer__slider skillcontainer__slider5">
        <Roll>
      <Linearprogress variant="determinate" value={75} />
      </Roll>
        </div>
    </div>
    <div className="skillcontainer__skillset">
    <Roll>
        <h5>JQUERY</h5>
        </Roll>
        <div className="skillcontainer__slider skillcontainer__slider6">
        <Roll>
      <Linearprogress variant="determinate" value={60} />
      </Roll>
        </div>
    </div>
    <div className="skillcontainer__skillset">
    <Roll>
        <h5>MY SQL</h5>
        </Roll>
        <div className="skillcontainer__slider skillcontainer__slider7">
        <Roll>
      <Linearprogress variant="determinate" value={60} />
      </Roll>
        </div>
    </div>
    <div className="clip1"></div>
    <div className="clip2"></div>

</div>


       </Element> 
      
            
        
    );  
};
export default Skillscontainer;